const S4 = function () {
    return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)
}
/**
 * @description: 生成GUID的方法
 * @return {string}:guid
 */
const guid = function () {
    return S4() + S4() + '-' + S4() + '-' + S4() + '-' + S4() + '-' + S4() + S4() + S4()
}
export default guid
